@import url(//fonts.googleapis.com/css2?family=Baloo+Chettan+2&family=Suez+One&display=swap);

$background: #edd3cdff;
$margin: 1rem;

#form {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;

  position: relative;
  margin-left: 10rem;

  padding: 6rem;
  transition-duration: 0.2s;

  // Restore password link
  & > a {
    margin-top: 5rem;
    align-self: flex-start;
    font-family: "Baloo Chettan 2", cursive, Arial, sans-serif;
  }

  @media (max-width: 830px) {
    margin-left: 0;
    transition-duration: 0.2s;
  }

  @media (max-width: 450px) {
    padding-left: 3rem;
    padding-right: 3rem;
    transition-duration: 0.2s;
  }

  label {
    margin-top: $margin;
    font-family: "Baloo Chettan 2", cursive, Arial, sans-serif;
  }

  input {
    width: 100%;
    background: $background;
    font-family: "Baloo Chettan 2", cursive, Arial, sans-serif;

    &:focus {
      border: 1px solid rgba(48, 136, 135, 1);
    }

    @media (max-width: 350px) {
      width: 80vw;
    }
  }

  button {
    border: 1px solid black;
    margin-top: $margin;
    font-family: "Suez One", Arial, sans-serif;
    box-shadow: 5px 5px 5px grey;
    transition-duration: 0.2s;

    &:active,
    &:focus {
      outline: none;
      box-shadow: 3px 3px 3px grey;
      transition-duration: 0.2s;
      text-decoration: none;
    }
  }

  input,
  button {
    padding: 3px;
    border-radius: 0.2rem;
  }

  // Error message
  p {
    margin-top: $margin;
    color: rgba(211, 56, 49, 1);
    word-break: keep-all;
    text-align: center;
  }
}

// Message 'email sent' on the restore password page
#restore_password_message {
  color: #15489f !important;
  font-family: "Baloo Chettan 2", cursive, Arial, sans-serif;
  font-size: 140%;
}
